.App {
  text-align: center;
  height: 99vh;
  width: 99vw;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

canvas {
  height: inherit;
  width: inherit;
}
#sidebar{
  grid-row: 1/-1;
  grid-column: 1;
}

#gen {
  margin-top: 100px;
  margin-left: 100px;
  margin-right: 100px;
  height: 40px;
}